const Tours = () => {
    return (
        <section className="tours">
            <h2>Search Tours</h2>
            <form action="#" className="tours-form">
                <div className="tours-form-group">
                    <select className="custom-select">
                        <option value="0">Destination</option>
                        <option value="1">Alexandria</option>
                        <option value="2">Aswan</option>
                        <option value="3">Cairo</option>
                        <option value="4">Dahab</option>
                        <option value="5">Giza</option>
                        <option value="6">Luxor</option>
                        <option value="7">Ras Sedr</option>
                        <option value="8">Moez Street</option>
                        <option value="9">Makkah</option>
                        <option value="10">Madina Monawrah</option>
                        <option value="11">Sharm ElSheikh</option>
                        <option value="12">Nuweiba</option>
                        <option value="13">Hurghada</option>
                    </select>
                </div>
                <div className="tours-form-group">
                    <input type="date" className="tours-form-input form-control" placeholder="Enter Date" />
                </div>
                <div className="tours-form-group">
                    <input type="number" className="tours-form-input form-control" placeholder="Enter Price From" />
                </div>
                <div className="tours-form-group">
                    <input type="number" className="tours-form-input form-control" placeholder="Enter Price To" />
                </div>
                <div className="tours-form-group">
                    <input type="submit" id="tours-form-submit" className="tours-form-submit form-control btn-primary" value="Search" />
                </div>
            </form>
        </section>
    );
};
export default Tours;