import React from 'react';
import Slider from 'react-slick';
import "../../node_modules/slick-carousel/slick/slick.css";
import "../../node_modules/slick-carousel/slick/slick-theme.css";
import { Link } from 'react-router-dom';
class Features extends React.Component {
    render() {
        const settings = {
            infinite: true,
            speed: 2500,
            slidesToShow: 4,
            slidesToScroll: 1,
            autoplay: true,
            arrows: false,
        };
        return (
            <section className="features">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-12">
                            <div className="features-txt">
                                <div className="features-title">
                                    <h2>Your World,</h2>
                                    <strong>Your Way</strong>
                                </div>
                                <p>
                                    With Lacoste Tavel You Will Find The Best Tour Price,
                                    The Best Tours, Best Tour Guide,
                                    You Will Find Your Comfort With Us
                                </p>
                            </div>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-lg-12">
                            <div className="features-slide">
                                <Slider {...settings}>
                                    <div className="slider-item">
                                        <div className="slider-icon">
                                            <i class="fad fa-mountains"></i>
                                        </div>
                                        <div className="slider-txt">
                                            <p>GREATNESS ARCHITECTURE</p>
                                            <p>PYRAMIDS</p>
                                        </div>
                                    </div>
                                    <div className="slider-item">
                                        <div className="slider-icon">
                                            <i class="fad fa-mountain"></i>
                                        </div>
                                        <div className="slider-txt">
                                            <p>SKIING</p>
                                            <p>
                                                Alps in
                                                <br />
                                                Swizerland
                                            </p>
                                        </div>
                                    </div>
                                    <div className="slider-item">
                                        <div className="slider-icon">
                                            <i class="fad fa-hiking"></i>
                                        </div>
                                        <div className="slider-txt">
                                            <p>Know The Adventure</p>
                                            <p>
                                                Everst
                                                <br />
                                                Mountain
                                            </p>
                                        </div>
                                    </div>
                                    <div className="slider-item">
                                        <div className="slider-icon">
                                            <i class="fad fa-parachute-box"></i>
                                        </div>
                                        <div className="slider-txt">
                                            <p>FLYING</p>
                                            <p>
                                                Looks At Luxor
                                                <br />
                                                From It's Sky
                                            </p>
                                        </div>
                                    </div>
                                    <div className="slider-item">
                                        <div className="slider-icon">
                                            <img src={process.env.PUBLIC_URL + '/imgs/slider-imgs/pisa-tower.png'} alt="" />
                                        </div>
                                        <div className="slider-txt">
                                            <p>Wonder's World</p>
                                            <p>In Your Hand</p>
                                        </div>
                                    </div>
                                    <div className="slider-item">
                                        <div className="slider-icon">
                                            <i class="fad fa-gift"></i>
                                        </div>
                                        <div className="slider-txt">
                                            <p>FREE GIIFT</p>
                                            <p>Souvenir</p>
                                        </div>
                                    </div>
                                    <div className="slider-item">
                                        <div className="slider-icon">
                                            <i class="fad fa-book-open"></i>
                                        </div>
                                        <div className="slider-txt">
                                            <p>THE KNOWLEDGE</p>
                                            <p>
                                                Alexandria
                                                <br />
                                                Library
                                            </p>
                                        </div>
                                    </div>
                                </Slider>
                            </div>
                        </div>
                    </div>
                    <div className="find-tour-btn">
                        <Link className='btn find-btn' to={"/"}>Find Your Tour</Link>
                    </div>
                </div>
            </section>
        );
    }
};
export default Features;