import React from 'react';
import { Link } from 'react-router-dom';
import Slider from 'react-slick';
import "../../node_modules/slick-carousel/slick/slick.css";
import "../../node_modules/slick-carousel/slick/slick-theme.css";
class Header extends React.Component {
    render() {
        const settings = {
            infinite: true,
            speed: 2500,
            slidesToShow: 1,
            slidesToScroll: 1,
            autoplay: true,
            pauseOnHover: true,
            arrows: true,
            fade: true,
            accessibility: true,
        };
        return (
            <header className="header container-fluid p-0">
                <div className="row g-0">
                    <div className="col-lg-12">
                        <div className="header-slider">
                            <Slider {...settings}>
                                <div className="header-slider-item">
                                    <div className="slider-img">
                                        <img src={process.env.PUBLIC_URL + '/imgs/slider-imgs/aswan.jpg'} alt="" />
                                    </div>
                                    <div className="slider-content wow bounceInDown"
                                        data-wow-iteration="infinite"
                                        data-wow-duration="4s">
                                        <p className='slogan'>South Egypt</p>
                                        <h1>
                                            <strong>Exiting</strong>
                                            <span>Aswan</span>
                                        </h1>
                                        <p>Between The Civilization And Beauty</p>
                                        <div className="discover-btns">
                                            <Link to={""} className="btn btn-primary">Discover Aswan</Link>
                                            <Link to={""} className="btn btn-secondary">Book Your Trip Now</Link>
                                        </div>
                                    </div>
                                </div>
                                <div className="header-slider-item">
                                    <div className="slider-img">
                                        <img src={process.env.PUBLIC_URL + '/imgs/slider-imgs/pyramids.jpg'} alt="" />
                                    </div>
                                    <div className="slider-content wow bounceInDown"
                                        data-wow-delay="3s"
                                        data-wow-iteration="infinite"
                                        data-wow-duration="4s">
                                        <p className='slogan'>EGYPT</p>
                                        <h1>
                                            <strong>The Magnificent</strong>
                                            <span>Pyramids of Giza</span>
                                        </h1>
                                        <p>The Greatness of Architecture</p>
                                        <div className="discover-btns">
                                            <Link to={""} className="btn btn-primary">Discover The Pyramids</Link>
                                            <Link to={""} className="btn btn-secondary">Book Your Trip Now</Link>
                                        </div>
                                    </div>
                                </div>
                                <div className="header-slider-item">
                                    <div className="slider-img">
                                        <img src={process.env.PUBLIC_URL + '/imgs/slider-imgs/luxor.jpg'} alt="" />
                                    </div>
                                    <div className="slider-content wow bounceInDown" 
                                        data-wow-iteration="infinite" 
                                        data-wow-delay="6s" 
                                        data-wow-duration="4s">
                                        <p className='slogan'>EGYPT</p>
                                        <h1>
                                            <strong>Irresistible</strong>
                                            <span>Luxor Balloon Tour</span>
                                        </h1>
                                        <p>A tour With A Balloon In Luxor's Sky Gives You a Greate and Comfort Experiance</p>
                                        <div className="discover-btns">
                                            <Link to={""} className="btn btn-primary">Discover Luxor</Link>
                                            <Link to={""} className="btn btn-secondary">Book Your Trip Now</Link>
                                        </div>
                                    </div>
                                </div>
                                <div className="header-slider-item">
                                    <div className="slider-img">
                                        <img src={process.env.PUBLIC_URL + '/imgs/slider-imgs/karnak.jpg'} alt="" />
                                    </div>
                                    <div className="slider-content wow bounceInDown" 
                                        data-wow-delay="9s" 
                                        data-wow-iteration="infinite" 
                                        data-wow-duration="4s">
                                        <p className='slogan'>UPPER EGYPT</p>
                                        <h1>
                                            <strong>Theatrical</strong>
                                            <span>Karnak Temple</span>
                                        </h1>
                                        <p>Karnak Temple Between Civilization And Architecture</p>
                                        <div className="discover-btns">
                                            <Link to={""} className="btn btn-primary">Discover Karnak Temple</Link>
                                            <Link to={""} className="btn btn-secondary">Book Your Trip Now</Link>
                                        </div>
                                    </div>
                                </div>
                            </Slider>
                        </div>
                    </div>
                </div>
            </header>
        );
    }
};
export default Header;