const Search = () => {
    return (
        <section className="search">
            <div className="container">
                <div className="row">
                    <div className="col-12">
                        <div className="search-box">
                            <form action="" method="post">
                                <input type="text" name="search" placeholder="Looking For" />
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
};
export default Search;