import React from 'react';
import Slider from 'react-slick';
import "../../node_modules/slick-carousel/slick/slick.css";
import "../../node_modules/slick-carousel/slick/slick-theme.css";
class Review extends React.Component {
    render() {
        const settings = {
            infinite: true,
            speed: 2500,
            slidesToShow: 1,
            slidesToScroll: 1,
            autoplay: true,
            pauseOnHover: true,
            arrows: false,
            fade: true,
            accessibility: true,
        };
        return (
            <div className="review">
                <div className="container-fluid">
                    <div className="row">
                        <div className="col-lg-12">
                            <div className="review-txt">
                                <div className="review-slogan">
                                    <p>Your Great Reviews What Makes Us Better</p>
                                </div>
                                <div className="review-title">
                                    <strong>Customer</strong>
                                    <span>Reviews</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="row g-0">
                        <div className="col-lg-6">
                            <div className="solid-bg">
                                <p>Facebook</p>
                            </div>
                        </div>
                        <div className="col-lg-6">
                            <div className="review-slide">
                                <Slider {...settings}>
                                    <div className="slider-item">
                                        <div className="slider-img">
                                            <img src={process.env.PUBLIC_URL + '/imgs/slider-imgs/1.png'} alt="" />
                                        </div>
                                    </div>
                                    <div className="slider-item">
                                        <div className="slider-img">
                                            <img src={process.env.PUBLIC_URL + '/imgs/slide-imgs/2.png'} alt="" />
                                        </div>
                                    </div>
                                </Slider>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}
export default Review;