const Religious = () => {
    return (
        <section className="religion">
            <div className="container">
                <div className="row">
                    <div className="col-lg-12">
                        <div className="religion-text wow fadeInUp" data-wow-delay="0.5s">
                            <p>EXCELLENT PLACES TO GO</p>
                            <span className="religion-span">Enjoy</span>
                            <h2>The Religious Tourism</h2>
                            <p>
                                Religious tourism is one of the oldest types of tourism,
                                and its aim is to perform religious rituals,
                                as is the case for Muslims from Hajj and Umrah,
                                as well as recreation and expansion of family members
                                as mentioned in the teachings of Islam.
                            </p>
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-lg-6 box rel-box">
                        <div className="religion-item wow fadeInUp" data-wow-delay="0.5s">
                            <div className="religion-img">
                                <img src={process.env.PUBLIC_URL + '/imgs/slider-imgs/mekkah.jpg'} alt="mekkah" />
                                <div className="price-label">
                                    <h3>$BASED ON HOTEL</h3>
                                </div>
                                <div className="religion-title">
                                    <h1>Mecca</h1>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-6 box rel-box">
                        <div className="religion-item wow fadeInUp" data-wow-delay="0.5s">
                            <div className="religion-img">
                                <img src={process.env.PUBLIC_URL + '/imgs/slider-imgs/madina.jpg'} alt="madina" />
                                <div className="price-label">
                                    <h3>$BASED ON HOTEL</h3>
                                </div>
                                <div className="religion-title text-center">
                                    <h1>Madina Monawra</h1>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
};
export default Religious;