import React from 'react';
import { Link } from 'react-router-dom';
import Index from './index';
const Nav = () => {
    return (
        <nav class="navbar navbar-expand-lg navbar-light bg-light">
            <div class="container-fluid">
                <Link class="navbar-brand" to={<Index />}>
                    <img className='logo' src={process.env.PUBLIC_URL + '/imgs/logo.jpg'} alt="logo" />
                </Link>
                <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                    <span class="navbar-toggler-icon"></span>
                </button>
                <div class="collapse navbar-collapse justify-content-end" id="navbarNav">
                    <ul class="navbar-nav">
                        <li class="nav-item">
                            <Link class="nav-link" aria-current="page" to={<Index />}>Home</Link>
                        </li>
                        <li class="nav-item">
                            <Link class="nav-link" aria-current="page" to={<Index />}>Inner Tourism</Link>
                        </li>
                        <li class="nav-item">
                            <Link class="nav-link" aria-current="page" to={"/"}>Outter Tourism</Link>
                        </li>
                        <li class="nav-item">
                            <Link class="nav-link" aria-current="page" to={"/"}>Services</Link>
                        </li>
                        <li class="nav-item">
                            <Link class="nav-link" to={"/"}>About Us</Link>
                        </li>
                        <li class="nav-item">
                            <Link class="nav-link" to={"/"}>Contact Us</Link>
                        </li> 
                        <li class="nav-item" id='search'>
                            <Link class="nav-link" to={"search"}>
                                <i class="fad fa-search"></i>
                            </Link>
                        </li>
                    </ul>
                </div>
            </div>
        </nav>
    );
}
export default Nav;